$colorGreen: #5BDAA5;
$colorBlack: #3A4151;
$colorWhite: #fff;
$colorPurple: #423F76;

@mixin size($w,$h:$w) {
    width: $w;
    height: $h; }

@mixin poa($t,$l:$t) {
    top: $t;
    left: $l;
    position: absolute; }

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center; }

body {
    font-family: Microsoft JhengHei;
    background: #aaaaaa; }
h1 {
    text-align: center;
    color: $colorGreen;
    font-size: 35px;
    font-weight: 900;
    text-shadow: 3px 3px 3px rgba(black,.5); }
.main {
    margin-top: 50px;
    select {
        padding: 3px 10px;
        border-radius: 5px;
        color: $colorPurple;
        font-family: Microsoft JhengHei;
        option {
            font-family: Microsoft JhengHei;
            border-radius: 5px;
            padding: 3px 10px; } }
    .mapWrap {
        margin-top: 50px;
        .mapView {
            float: left;
            width: 50%;
            #map {
                box-sizing: border-box;
                @include size(90%,370px);
                background: #000;
                border-radius: 5px; } }
        .mapContent {
            float: left;
            width: 50%;
            #listitem {
                height: 300px;
                overflow-y: scroll;
                .dropDown {
                    position: relative;
                    font-size: 22px;
                    color: $colorPurple;
                    font-weight: 900;
                    padding-bottom: 10px;
                    cursor: pointer;
                    .submenu {
                        box-sizing: border-box;
                        z-index: 9999;
                        display: none;
                        border-radius: 5px;
                        padding: 10px;
                        background: white;
                        position: absolute;
                        top: 20px;
                        left: -5px;
                        margin-top: 10px;
                        font-size: 16px;
                        line-height: 1.4;
                        p {
                            font-weight: 600;
                            color: black;
                            span {
                                color: rgba($colorGreen,.9); } } } } } } } }



// Theme
.wrap {
    padding: 50px;
    margin: 0 auto;
    box-sizing: border-box;
    position: relative;
    width: 960px; }
.clearfix {
    clear: both; }

